<template>
    <div>
        <div v-if="show" style="position:fixed;top:0;left:0;width:100vw;height:100vh;background:black;opacity:0.3;">

        </div>
        <div v-if="show" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:10;">
            <div style="width:100%;" class="bg-white p-3 shadow-md rounded-lg p-3">
                <p class="text-xl p-3">
                    {{pilih.translation_id}}
                </p>
            </div>
        </div>
        <div class="container" >
            <div class="row justify-content-center sm:p-4" v-if="datanya.length>0">
                <div class="col-12 bg-white p-3 ">
                    <p class="p-3 text-center text-2xl font-bold font-italic">
                        ASMAUL HUSNA
                    </p>
                </div>
                <div class="col-6 col-sm-4 cursor-pointer" @click="pilih=item;show=!show;" v-for="(item, index) in datanya" :key="index+'asmaulhusna'">
                    <div class="rounded-lg shadow bg-white mt-2 p-3">
                        <!-- <span>
                            {{item.index}}
                        </span> -->
                        <p class="text-center font-semibold text-4xl" style="font-family: 'Amiri', serif;line-height: 2.0;">
                            {{item.arabic}}
                        </p>
                        <p class="text-sm text-center font-semibold mt-2">
                            {{item.latin}}
                        </p>
                        <p></p>
                    </div>
                </div>
            </div>
            <div v-else>
                <div style="height:90vh;" class="d-flex justify-content-center align-items-center ">
                    <div style="width:100%">
                          <p class="text-center animated bounce infinite text-lg font-semibold">
                                Loading ...
                            </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    layout:'app_islamic',
    data(){
        return{
            datanya:[],
            show:false,
            pilih:{}
        }
    },
    methods: {
        getData(){
            axios.get('https://islamic-api-zhirrr.vercel.app/api/asmaulhusna').then(res=>{
                this.datanya=res.data.data
                // console.log(res.data.data)
                this.$forceUpdate();
            })
        }
    },
    mounted() {
        let that=this;
     function onBackKeyDown(e){
          e.preventDefault();
          e.stopPropagation();
          that.$router.push('/app/islamic')
        };
        document.addEventListener("backbutton", onBackKeyDown, false);
        this.getData()
    },
      metaInfo: {
        title: `Baca Al Qur'an`,
        link: [
         { rel: 'preconnect', href: 'https://fonts.googleapis.com' },
         { rel: 'preconnect', href: 'https://fonts.gstatic.com' },
         { rel: 'stylesheet', href: 'https://fonts.googleapis.com/css2?family=Amiri&display=swap' },
        ],
    },
}
</script>